<template>
    <div class="as">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <!--  -->
            <el-row>
                <el-col><el-button type="primary" @click="addRolesRole"></el-button></el-col>
            </el-row>

            <!--  -->
            <el-table :data="rolesList" border stripe>
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-row v-for="(item1, i1) in scope.row.children" :key="item1.id" :class="['bdbottom', i1 === 0 ? 'bdtap' : '', 'vcenter']">
                            <!--  -->
                            <el-col :span="5">
                                <el-tag closable @close="removeRightByDd(scope.row, item1.id)">{{ item1.authName }}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!--  -->
                            <el-col :span="19">
                                <!-- for -->
                                <el-row v-for="(item2, i2) in item1.children" :key="item2.id" :class="[i2 === 0 ? '' : 'bdtap', 'vcenter']">
                                    <el-col :span="6">
                                        <el-tag type="success" closable @close="removeRightByDd(scope.row, item2.id)">{{ item2.authName }}</el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    <el-col :span="18">
                                        <el-tag v-for="item3 in item2.children" :key="item3.id" type="warning" closable @close="removeRightByDd(scope.row, item3.id)">
                                            {{ item3.authName }}
                                        </el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                        <!-- <pre>
                            {{ scope.row }}
                        </pre> -->
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="roleName"></el-table-column>
                <el-table-column label="" prop="roleDesc"></el-table-column>
                <el-table-column label="" width="300px">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" icon="el-icon-edit" @click="showCompileDialog(scope.row)"></el-button>
                        <el-popconfirm title="" @onConfirm="deleteRole(scope.row)">
                            <el-button slot="reference" size="mini" type="danger" icon="el-icon-delete" class="marginButton"></el-button>
                        </el-popconfirm>

                        <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
            <!--  -->
            <el-tree :data="rightlist" :props="treeProps" default-expand-all show-checkbox node-key="id" :default-checked-keys="defKefs" ref="treeRef"> </el-tree>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRightDialogVisible = false"> </el-button>
                <el-button type="primary" @click="allotRight"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="addRolesVisible" width="50%" @close="addRolesClose">
            <el-form :model="addRolesForm" :rules="addRolesRules" ref="addRolesRef" label-width="100px">
                <el-form-item label="" prop="name">
                    <el-input v-model="addRolesForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="describe">
                    <el-input v-model="addRolesForm.describe"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addRolesVisible = false"> </el-button>
                <el-button type="primary" @click="confirmAddRoles"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="compileRolesVisible" width="50%">
            <el-form :model="compileRuleForm" :rules="compileRolesRules" ref="compileRoleRef" label-width="100px">
                <el-form-item label="" prop="name">
                    <el-input v-model="compileRuleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="compileRuleForm.desc"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="compileRolesVisible = false"> </el-button>
                <el-button type="primary" @click="confirmCompile"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // 
            rolesList: [],
            // 
            setRightDialogVisible: false,
            // 
            rightlist: [],
            // 
            treeProps: {
                label: 'authName',
                children: 'children'
            },
            // id
            defKefs: [],
            // id
            roleId: '',
            // 
            addRolesForm: {
                name: '',
                describe: ''
            },
            // 
            addRolesRules: {
                name: [{ required: true, message: '', trigger: 'blur' }],
                describe: [{ required: true, message: '', trigger: 'blur' }]
            },
            // 
            addRolesVisible: false,
            // 
            compileRolesVisible: false,
            // 
            compileRuleForm: {
                name: '',
                desc: '',
                id: ''
            },
            compileRolesRules: {
                name: [{ required: true, message: '', trigger: 'blur' }],
                desc: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created() {
        this.getRolesList()
    },
    methods: {
        async getRolesList() {
            const { data: res } = await this.$http.get('roles')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.rolesList = res.data
        },
        // id
        async removeRightByDd(role, RightId) {
            console.log(role, RightId)
            // 
            const confirmResult = await this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).catch(err => err)
            if (confirmResult !== 'confirm') {
                return this.$message.info('')
            }
            const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${RightId}`)
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success(res.meta.msg)
            // this.getRolesList()
            console.log(role)
            console.log(res)
            role.children = res.data
        },
        // 
        async showSetRightDialog(role) {
            this.roleId = role.id
            // 
            const { data: res } = await this.$http.get('rights/tree')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            // 
            this.rightlist = res.data
            console.log(this.rightlist)
            // id
            this.getLeafKeys(role, this.defKefs)
            this.setRightDialogVisible = true
        },
        // id,defKeys 
        getLeafKeys(node, arr) {
            // node  children
            if (!node.children) {
                return arr.push(node.id)
            }
            node.children.forEach(item => {
                this.getLeafKeys(item, arr)
            })
        },
        // 
        setRightDialogClosed() {
            this.defKefs = []
        },
        // 
        async allotRight() {
            const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
            const idStr = keys.join(',')
            console.log()
            const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('')
            this.getRolesList()
            this.setRightDialogVisible = false
        },
        // 
        addRolesRole() {
            this.addRolesVisible = true
        },
        // addRolesForm
        addRolesClose() {
            this.$refs.addRolesRef.resetFields()
        },
        // 
        confirmAddRoles() {
            this.$refs.addRolesRef.validate(async vaild => {
                if (!vaild) return null
                const { data: res } = await this.$http.post('roles', {
                    roleName: this.addRolesForm.name,
                    roleDesc: this.addRolesForm.describe
                })
                if (res.meta.status !== 201) {
                    return this.$message.error(res.meta.msg)
                }
                this.$message.success(res.meta.msg)
                this.getRolesList()
                this.addRolesVisible = false
                console.log(res)
            })
        },
        // 
        showCompileDialog(role) {
            this.compileRuleForm.name = role.roleName
            this.compileRuleForm.desc = role.roleDesc
            this.compileRuleForm.id = role.id
            this.compileRolesVisible = true
        },
        // 
        confirmCompile() {
            console.log(this.compileRuleForm)
            this.$refs.compileRoleRef.validate(async vaild => {
                if (!vaild) return null
                const { data: res } = await this.$http.put(`roles/${this.compileRuleForm.id}`, {
                    roleName: this.compileRuleForm.name,
                    roleDesc: this.compileRuleForm.desc
                })
                if (res.meta.status !== 200) {
                    return this.$message.error(res.meta.msg)
                }
                this.$message.success('')
                this.getRolesList()
                this.compileRolesVisible = false
            })
        },
        // 
        async deleteRole(row) {
            const { data: res } = await this.$http.delete(`roles/${row.id}`)
            if (res.meta.status !== 200) {
                return this.message.error(res.meta.msg)
            }
            this.$message.success('')
            this.getRolesList()
        }
    }
}
</script>

<style lang="less" scoped>
.el-tag {
    margin: 7px;
}
.bdtap {
    border-top: 1px solid #eee;
}
.bdbottom {
    border-bottom: 1px solid #eee;
}
.vcenter {
    display: flex;
    align-items: center;
}
.marginButton {
    margin: 0 10px;
}
</style>
