<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" class="input-with-select" v-model="queryInfo.query" clearable @clear="getUserList">
                        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <el-table :data="userList" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="username"></el-table-column>
                <el-table-column label="" prop="email"></el-table-column>
                <el-table-column label="" prop="mobile"></el-table-column>
                <el-table-column label="" prop="role_name"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <!-- {{scope.row.mg_state}} -->
                        <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="180px">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        <el-button type="warning" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
                        <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="primary" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[1, 2, 5, 10]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
            ></el-pagination>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
            <!--  -->
            <el-form :model="addFrom" :rules="addFromRules" ref="addFormRef" label-width="70px">
                <el-form-item label="" prop="username">
                    <el-input v-model="addFrom.username"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="addFrom.password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input v-model="addFrom.email"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mobile">
                    <el-input v-model="addFrom.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addUser"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="editDialogVisble" width="50%" @close="editDialogClosed">
            <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px">
                <el-form-item label="">
                    <el-input v-model="editForm.username" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input v-model="editForm.email"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mobile">
                    <el-input v-model="editForm.mobile"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisble = false"> </el-button>
                <el-button type="primary" @click="editUserInfo"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="setRoleDialogVistble" width="50%" @close="setRoleDiaogClosed">
            <div>
                <p>{{ userInfo.username }}</p>
                <p>{{ userInfo.role_name }}</p>
                <p>
                    
                    <el-select v-model="selectRoleID" placeholder="">
                        <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id"> </el-option>
                    </el-select>
                </p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRoleDialogVistble = false"> </el-button>
                <el-button type="primary" @click="saveRoleInfo"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        // 
        var checkEmail = (rule, value, cb) => {
            const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
            if (regEmail.test(value)) {
                return cb()
            }
            cb(new Error(''))
        }
        //  
        var checkMobile = (rule, value, cb) => {
            const regEmail = /^[1][3,4,5,7,8][0-9]{9}$/
            if (regEmail.test(value)) {
                return cb()
            }
            cb(new Error(''))
        }
        return {
            // 
            queryInfo: {
                query: '',
                // 
                pagenum: 1,
                // 
                pagesize: 2
            },
            userList: [],
            total: 0,
            // 
            addDialogVisible: false,
            // 
            addFrom: {
                username: '',
                password: '',
                email: '',
                mobile: ''
            },
            // 
            addFromRules: {
                username: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 3,
                        max: 10,
                        message: ' 3~10 ',
                        trigger: 'blur'
                    }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    {
                        min: 6,
                        max: 10,
                        message: ' 6~15 ',
                        trigger: 'blur'
                    }
                ],
                email: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkEmail, trigger: 'blur' }
                ],
                mobile: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    { validator: checkMobile, trigger: 'blur' }
                ]
            },
            // 
            editDialogVisble: false,
            // 
            editForm: {},
            // 
            editFormRules: {
                email: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkEmail, trigger: 'blur' }
                ],
                mobile: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    { validator: checkMobile, trigger: 'blur' }
                ]
            },
            // 
            setRoleDialogVistble: false,
            // 
            userInfo: [],
            // 
            rolesList: [],
            // id
            selectRoleID: ''
        }
    },
    created() {
        this.getUserList()
    },
    methods: {
        async getUserList() {
            const { data: res } = await this.$http.get('users', {
                params: this.queryInfo
            })
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.userList = res.data.users
            this.total = res.data.total
        },
        //   pagesize 
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getUserList()
        },
        // 
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getUserList()
        },
        // switch 
        async userStateChanged(userinfo) {
            console.log(userinfo)
            const { data: res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
            if (res.meta.status !== 200) {
                userinfo.mg_state = !userinfo.mg_state
                return this.$message.error('')
            }
            this.$message.success('')
        },
        // 
        addDialogClosed() {
            this.$refs.addFormRef.resetFields()
        },
        // 
        addUser() {
            this.$refs.addFormRef.validate(async valid => {
                if (!valid) return null
                const { data: res } = await this.$http.post('users', this.addFrom)
                if (res.meta.status !== 201) {
                    this.$message.error('')
                }
                this.$message.success('')
                // 
                this.addDialogVisible = false
                this.getUserList()
            })
        },
        // 
        async showEditDialog(id) {
            this.editDialogVisble = true
            const { data: res } = await this.$http.get(`users/${id}`)
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.editForm = res.data
        },
        // 
        editDialogClosed() {
            this.$refs.editFormRef.resetFields()
        },
        // 
        editUserInfo() {
            this.$refs.editFormRef.validate(async valid => {
                if (!valid) return null
                const { data: res } = await this.$http.put(`users/${this.editForm.id}`, {
                    email: this.editForm.email,
                    mobile: this.editForm.mobile
                })
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                // 
                this.editDialogVisble = false
                // 
                this.getUserList()
                // 
                this.$message.success('')
            })
        },
        // id
        async removeUserById(id) {
            try {
                const confirmResult = await this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }) // .catch(err => err)
                if (confirmResult === 'confirm') {
                    const { data: res } = await this.$http.delete(`users/${id}`)
                    if (res.meta.status !== 200) {
                        return this.$message.error('')
                    }
                    this.$message.success('')
                    this.getUserList()
                }
            } catch (error) {
                return this.$message.info('')
            }
        },
        // 
        async setRole(userInfo) {
            this.userInfo = userInfo
            // 
            const { data: res } = await this.$http.get('roles')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.rolesList = res.data
            this.setRoleDialogVistble = true
        },
        // 
        async saveRoleInfo() {
            if (!this.selectRoleID) {
                return this.$message.error('')
            }
            const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, { rid: this.selectRoleID })
            if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg)
            }
            this.$message.success(res.meta.msg)
            this.getUserList()
            this.setRoleDialogVistble = false
        },
        // 
        setRoleDiaogClosed() {
            this.selectRoleID = ''
            this.userInfo = ''
        }
    }
}
</script>

<style lang="less" scoped></style>
